export default {
	data() {
		return {
			number:0,
			height:320,
			startTime:0,
			calendar:[],
			current:0,
			startTimeDate:'',
			startTimeHour:'',
			startTimeMinute:'',
			showStart:0,
			
			endTime:0,
			calendar2:[],
			current2:0,
			showEnd:0,
			endTimeDate:'',
			endTimeHour:'',
			endTimeMinute:'',
		}
	},
	mounted() {
		this.getCurrentDate();
	},
	methods:{
		returnfalseDate(){
			this.calendar.forEach(item=>{
				item.active = false
			})
		},
		returnfalseHour(){
			let arr = this.calendar[this.current].hour
			arr.forEach(item=>{
				item.active = false
			})
		},
		returnfalseMinute(){
			let arr = this.calendar[this.current].minute
			arr.forEach(item=>{
				item.active = false
			})
		},
		
		showStartTimer(){
			this.showStart = 1;
			this.showEnd = 0;
			this.returnfalseDate();
			this.returnfalseHour();
			this.returnfalseMinute();
		},
		
		changeOne(i,n,type=1){
			this.current = n;
			
			this.returnfalseDate();
			i.active = true;
			if(type == 1){
				
				this.startTime = 1;
				this.startTimeDate = i.year+i.month+i.day;
			}else{
				
				this.endTime = 1;
				this.endTimeDate = i.year+i.month+i.day;
			}
		},
		changeTwo(i,type=1){
			
			this.returnfalseHour();
			i.active = true;
			if(type == 1){
				
				this.startTimeHour = (i.hour<10)? ('0'+i.hour): i.hour ;
			}else{
				
				this.endTimeHour = (i.hour<10)? ('0'+i.hour): i.hour ;
			}
		},
		changeThree(i,type=1){
			
			this.returnfalseMinute();
			i.active = true;
			if(type == 1){
				
				this.startTimeMinute =  i.minute;
			}else{
				
				this.endTimeMinute =  i.minute;
			}
		},
		
		showEndTimer(){
			this.showStart = 0;
			this.showEnd = 1;
			this.returnfalseDate();
			this.returnfalseHour();
			this.returnfalseMinute();
		},
	
		close(){
			this.$emit("close");
		},
		submit(){
			console.log('提交')
		},
		getCurrentDate(format){
			var now = new Date();
			var year = now.getFullYear();
			var month = now.getMonth();
			var date = now.getDate();
			var day = now.getDay();
			var hour = now.getHours();
			month = month + 1;
			
			let result = [];
			result.push(
				{
					week:'今天',
					year:year+'年',
					day:date+'日',
					month:month+'月',
					select:true,
					active:false,
					i:0,
					hour:this.getHours(hour+1),
					minute:this.getMinute()
				}
			);
			let height =  70 / 750 * wx.getSystemInfoSync().windowWidth;
			for(let i = 1; i<=10; i++){
				let oTime =  this.getDay(+i)
				result.push(oTime);
			}
			this.calendar = result;
		},
		getDay(num) {
			var today = new Date();
			var nowTime = today.getTime();
		    var ms = 24*3600*1000*num;
		    today.setTime(parseInt(nowTime + ms));
		    var oYear = today.getFullYear();
		    var oMoth = (today.getMonth() + 1).toString();
		    var oDate = today.getDate().toString();
			var oday = today.getDay();
			var oyear = today.getFullYear();
			return {
					week:'星期'+ this.returnWeek(oday),
					day:oDate+'日',
					year:oyear+'年',
					month:oMoth+'月',
					select:false,
					active:false,
					i:num,
					hour:this.getHours(0),
					minute:this.getMinute()
				}
		},
		getMinute(){
			let arr = [];
			for(var i = 1;i<7;i++){
				let minute1 = i+'0分';
				arr.push({
					minute1:minute1,
					active:false,
					minute:i+'0',
				})
			}
			return  arr;
		},
		getHours(hour = 0){
			let clock2 = [];
			for(let i=hour;i<=23;i++){
				let hour1 = i+'点';
				clock2.push({
					hour1:hour1,
					active:false,
					hour:i,
				})
			}
			return  clock2;
		},
		returnWeek(i){
			let week = '';
			if(i == 0 || i == 7){
				week = "日"
			}else if(i == 1){
				week = "一"
			}else if(i == 2){
				week = "二"
			}else if(i == 3){
				week = "三"
			}else if(i == 4){
				week = "四"
			}else if(i == 5){
				week = "五"
			}else if(i == 6){
				week = "六"
			}
			return week;
		}
	}
}
